.blog {
  @include tablet {
    background-image: linear-gradient(
      rgba(47, 48, 58, 0.4),
      rgba(47, 48, 58, 0.4)
    ),
    url(../images/blog/backgr-tabl-min.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
  
  @include desktop{
  
  background-image: linear-gradient(
      rgba(47, 48, 58, 0.4),
      rgba(47, 48, 58, 0.4)
    ),
    url(../images/blog/backgr-desktop-blog-min.jpg);
  background-repeat: no-repeat;
  background-position: 50% 95%;
  background-size: cover;
  
}}

.blog-container {
  overflow: hidden;
}
.blog {
  display: flex;
  flex-direction: column;
}

.blog-title {
  position: relative;
  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-align: left;
 
  color: var(--white-color-text);
  padding-top: 40px;
  padding-bottom: 4px;
  @include tablet {
    padding-top: 120px;
    font-size: 56px;
    margin-bottom: 11px;
    padding-left: 83px;
  }
  @include desktop {
    margin-bottom: 8px;
    padding-left: 102px;
    padding-top: 120px;
  }

  &::before {
    content: ' ';
    position: absolute;
    background-size: cover;
    background-position: center;
    width: 480px;
    height: 30px;
    top: 63px;
    left: -16px;
    background-image: url(../images/blog/title-top-line-mob.png);

    @media screen and (max-width: $mobile) {
      width: 100vw;
    }

    @include tablet {
      width: 1280px;
      height: 30px;
      left: -190px;
      top: 187px;
      background-image: url(../images/blog/title-top-line.png);
    }

    @include desktop {
      width: 1280px;
      height: 30px;
      top: 180px;
      left: -46px;
      background-image: url(../images/blog/title-top-line-desk.png);
    }
  }

  &::after {
    content: ' ';
    position: absolute;
    background-size: cover;
    background-position: center;
    width: 480px;
    height: 30px;
    top: 66px;
    left: -16px;
    background-image: url(../images/blog/title-bottom-line-mob.png);

    @media screen and (max-width: $mobile) {
      width: 100vw;
    }

    @include tablet {
      width: 1280px;
      height: 30px;
      left: -190px;
      top: 195px;
      background-image: url(../images/blog/title-bottom-line.png);
    }

    @include desktop {
      width: 1280px;
      height: 30px;
      top: 190px;
      left: -46px;
      background-image: url(../images/blog/title-bottom-line-desk.png);
    }
  }
}

.photo {
  width: 100%;
  background-size: cover;
  padding-left: 0px;
  padding-right: 0px;
  @include tablet {
    width: 387px;
    border-radius: 12px;
    height: 228px;
  }
  @include desktop {
    width: 387px;
    height: 364px;
  }
}
.blog-list {
  width: 100%;
  list-style: none;
  margin-top: 20px;
  @include tablet {
    margin-top: 50px;
    width: 688px;
  }
  @include desktop {
    width: 1200px;
    display: block;
    margin-top: 48px;
  }
}
.cards {
  @media screen and (min-width: 320px) and (max-width: 479px) {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }

  @include desktop {
    width: 1200px;
    display: flex;
    flex-direction: row;
  }
}

.cards:not(:last-child) {
  margin-bottom: 24px;
  @include tablet {
    margin-bottom: 44px;
  }
  @include desktop {
    margin-bottom: 40px;
  }
}
.cards-title {
  font-weight: 500;
  font-size: 24px;
  line-height: 1.17;
  text-align: left;
  
  color: var(--white-color-text);
  margin-bottom: 16px;
 
  @include tablet {
    font-size: 56px;
    line-height: 1;
  }
  @include desktop {
    margin-bottom: 52px;
  }
}
.blog-text-wrap {
  @include desktop {
    display: block;
    width: 693px;
  }
}

.blog-photo-subscribe {
  @include tablet {
    display: flex;
    flex-direction: row;
  }
}
.blog-photo-desktop {
  @media screen and (min-width: 320px) and (max-width: 479px) {
    display: none;
  }
  @include mobile {
    display: none;
  }
  @include tablet {
    display: none;
  }
  @include desktop {
    display: block;
    margin-right: 120px;
  }
}

.blog-photo {
  @media screen and (min-width: 320px) and (max-width: 767px) {
    margin-left: -15px;
    margin-right: -15px;
  }
  @include tablet {
    margin-right: 40px;
    @include desktop {
      display: none;
    }
  }
}
.article-subscribe {
  margin-top: 8px;
  @include tablet {
    padding-top: 100px;
    @include desktop {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding-top: 0px;
      align-items: baseline;
      margin-top: 0px;
      width: 692px;
      margin-bottom: 24px;
    }
  }
}
.blog-author {
  font-weight: 500;
  font-size: 16px;
  line-height: 1;

  color: var(--white-color-text);
  @include tablet {
    width: 248px;
    font-size: 24px;
  }
  @include desktop {
    width: 249px;
    height: 24px;
    font-size: 24px;
  }
}


  .blog-updates {
    font-weight: 500;
    font-size: 16px;
    line-height: 1;
    text-align: left;
    letter-spacing: 0.03em;
    margin-top: 8px;

    color: #8d9289;
    @include tablet {
      font-weight: 500;
      min-width: 261px;
      font-size: 24px;
      margin-top: 32px;
    }
    @include desktop {
      min-width: 347px;
      height: 24px;
      font-size: 23px;
      margin-left: 96px;
      margin-top: 0;
    }
  }


.article-text {
  margin-top: 16px;
  display: block;
  @include desktop {
    margin-top: 24px;
  }
}
.blog-text {
  display: block;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.19;
  text-align: left;
  margin-bottom: 8px;
  color: var(--white-color-text);
  
  @include tablet {
    font-weight: 400;
    font-size: 24px;
    line-height: 1.17;
   
  }
  @include desktop {
    margin-bottom: 16px;
  }
}
.blog-text-newline{
  
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 1.19;
    text-align: left;
    margin-bottom: 8px;
    color: var(--white-color-text);
    
    @include tablet {
      font-weight: 400;
      font-size: 24px;
      line-height: 1.17;
     
    }
    @include desktop {
      margin-bottom: 16px;
    }
}
.blog-moreinfo-link {
  display: block;
  font-weight: 500;
  font-size: 16px;
  line-height: 1;
  text-align: right;
  color: var(--light-green-color);
  @include tablet {
    font-size: 24px;
    line-height: 1;
    margin-top: 16px;
  }
}
.slider{
  display:block;
  margin-top:48px;
  margin-bottom: 48px;
  display: flex;
  align-items: center;
  justify-content: center;

  flex-direction: row;

  @include tablet{
    margin-top:40px;
    margin-bottom: 120px;
  }
 
 
 }
  .blog-slider {
 
   height: 16px;
   width: 104px;
   
   position: relative;
  
 
  }
  

  .blog-dots {
     
   background-color: var(--white-color-text);
    width:16px;
    height: 16px;
    border-radius:50%;
    border: 1px solid var(--white-color-text);
   
    margin-left:16px;
    animation: move 700ms linear 0ms infinite;
    margin-right: 30px;
    
    &:first-child{
      position: absolute;
      top:0;
      left:0;
      animation: grow 700ms linear 0ms infinite;
    }
    
    &:last-child{
      position: absolute;
      top: 0;
      right: 0;
      margin-right: 0;
      animation: grow 700ms linear 0s infinite reverse;
    }
  }

  
  .oval{
    background-color: var(--white-color-text);
    width:40px;
    height: 16px;
    border-radius:30%;
    border: 1px solid var(--white-color-text);
  }

  @keyframes grow {
    from {transform: scale(0,0); opacity: 0;}
    to {transform: scale(1,1); opacity: 1;}
  }
  
  @keyframes move {
    from {transform: translateX(0px)}
    to {transform: translateX(45px)}
  }